<template>
    <div class="page-shop">
       <div class="left">
           <div class="user-info">
                <div class="avatar">
                   <img :src="require('../assets/avatar/'+3+'.png')" />
                </div>
                <div class="vip">
                   <img :src="require('../assets/vip/'+userInfo.vip_level+'.png')"/>
                </div>
                <div class="username">
                   {{userInfo.nickname}}
                </div>
                <div class="coin">
                    <b>{{gold}}</b>
                </div>
                <div class="today">
                    <b> {{num}} </b>
                </div>
                <div class="btn">
                    <el-button @click="$router.push({ path: '/shop/order' })" class="button"></el-button>
                </div>
                <div class="btn">
                    <el-tooltip 
                        popper-class="rules"
                        effect="dark" 
                        enterable
                        visible-arrow="true"
                        placement="right">
                        <div slot="content">

                            <div class="row">
                                <h3 class="title">()</h3>
                                <div class="content">
                                    1<br />
                                    2<br />
                                    33 <br />
                                </div>
                            </div>

                            <div class="row">
                                <h3 class="title"></h3>
                                <div class="content">
                                    1<br />
                                    2<br />
                                    3<br />
                                    4<br />
                                    5<br />                                
                                </div>
                            </div>

                            <div class="row">
                                <h3 class="title"></h3>
                                <div class="content">
                                    1<br />
                                    2<br />
                                    3<br />
                                    4<br />            
                                </div>
                            </div>
                        </div>
                        <el-button class="button btnRules" ></el-button>
                    </el-tooltip>
                </div>
           </div>
            <div class="vip">
                <h3 class="text">
                    VIP
                </h3>
                <div v-for="item in vipList" :key="item.id" class="user-block">
                    <component-user-head
                        :userID="item.id"
                        :avatar="utils.avatarFilter(item.user_icon)"
                        :nickName="item.nickname"
                    >
                        <template #float>
                            <img height="20" :src="require('../assets/vip/'+userInfo.vip_level+'.png')" />
                        </template>
                        <!-- <template #time>
                            
                        </template> -->
                        <template #right>
                            <img width="50" height="50" :src="item.goods_thumb_img" />
                        </template>
                    </component-user-head>
                </div>
           </div>
       </div>
       <div class="right">
            <router-view  @dataRefresh="dataRefresh" />
       </div>
    </div>
</template>

<script>
import ComponentUserHead from '../components/ComponentUserHead';
export default {
    components: {
        ComponentUserHead
    },
    data() {
        return {
            vipList: [],
            PRICE: 0,
            COUNT: 0,
            userInfo: {},
            gold:0,
            num:0,
        };
    },
    mounted() {

// gold: 99995510
// icon: "1"
// nickname: "00973"
// num: 983
// vip_level: 0

        this.utils.userInfo().then((response) => {
            let { data } = response;
            if (data.code == 0) {
                this.userInfo = data.data;
                this.gold = this.userInfo.gold;
                this.num = this.userInfo.num;
            }
        });
        this.$http
        .post(this.config.HOST + 'api/order/log', {})
        .then(response => {
             let result = response.data;
            if (result.code == 0) {
                let { data } = result;
                this.vipList = data;
            }
        });
    },
    methods: {
        handleClick(tab, event) {
            console.log(tab, event);
        },
        dataRefresh(value){
            this.gold = this.gold - value;
            this.num --;
        }
        
    }
}
</script>
<style lang="less" scoped>
    @import url('../assets/style.less');
    .page-shop {
        width: 1234px;
        margin: auto;
        display: flex;
        .left {
            width: 310px;
            min-height:950px;
            .user-info {
                background-color: @color-line;
                height: 413px;
                display: flex;
                flex-direction: column;
                align-items: center;
                text-align: center;
                .avatar {
                    padding: 30px 0 0 0;
                    img {
                        width: 95px;
                        height: 95px;
                        border-radius: 50%;
                    }
                }
                .vip{
                    width:80px;
                    img{
                        width: 100%;
                    }
                }
                .username {
                    padding: 10px 0 20px 0;
                    color: #fff;
                }
                .coin, .today {
                    font-size: 14px;
                    color: @color-used;
                    margin: 0 0 10px 0;
                    b {
                        color: #27F3CB;
                    }
                }
                .today {
                    margin: 0 0 35px 0;
                }
                .btn {
                    margin: 0 0 10px 0;
                    .button {
                        width: 125px;
                        height: 28px;
                        padding: 0;
                        border-style:none;
                        border: 1px solid @color-two-level;
                        border-radius: 2px;
                        background-color: transparent;
                        color: #00b3f1;
                        font-size: 12px;
                        color: @color-used;
                        
                    }
                    .btnRules:hover {
                        border-color: @color-click;
                        color: @color-click;
                    }
                }
            }
            .vip {
                
                .text {
                    margin: 3px 0 0 0;
                    height: 48px;
                    line-height: 48px;
                    background-color: @color-line;
                    text-align: center;
                    color: #fff;
                    font-weight: normal;
                }
                .user-block {
                    background-color: #0C0F1A;
                    border-bottom: 1px solid @color-line;
                }
            }
        }
        .right {
            margin-left: 6px;
            background-color: @color-bg;
        }
    }
</style>
